<template>
  <div class="header-block">
    <div class="container-fluid height_full dark_background">
      <div class="row align-items-center height_full">
        <div class="col">
          <h1>{{ header }}</h1>
          <p>{{ desc }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Aboute',
  data () {
    return {
      header: 'Я - Юрий Щербаченко',
      desc: 'html-верстальщик, фрилансер'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header-block {
  background: url('../assets/images/mpc1.jpg') no-repeat 0 0;
  -webkit-background-size: 100% auto;
  background-size: 100% auto;
  height: 100vh;
}
.dark_background {
  background: rgba(0, 0, 0, .4);
}
h1, h2 {
  font-size: 37pt;
  font-weight: normal;
  color: #fff;
}
p {
  color: #faeada;
  font-size: 27.5pt;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



