<template>
  <div class="my_skills">
    <div class="container height_full">
      <h2>{{ header }}</h2>
      <p>
        Выполняю Html-верстку (стандарты W3C, валидный код), создаю html-письма для e-mail рассылок. Разрабатываю с нуля сайты различной тематики: визитки, блоги, интернет-магазины, Landing Page и т.д., выполняю редизайн. Есть опыт кросбраузерной и адаптивной верстки, ваш сайт будет корректно работать на разных устройствах. Цена зависит от сложности макета и сроков.
      </p>
      <div class="skill_icons">
        <div class="row align-items-center height_full">
          <div class="col"><img src="../assets/images/HTML.svg" alt="html"></div>
          <div class="col"><img src="../assets/images/vuejs.svg" alt="vuejs"></div>
          <div class="col"><img src="../assets/images/sass-logo.svg" alt="sass"></div>
          <div class="col"><img src="../assets/images/nodejs.svg" alt="js"></div>
          <div class="col"><img src="../assets/images/css3.svg" alt="css"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomePage',
  data () {
    return {
      header: 'Почему я'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  margin-bottom: 30px
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
p {
  font-family: 'Avenir';
  text-align: left;
}
.my_skills {
  min-height: 500px;
  padding-top: 50px
}
.skill_icons {
  height: auto;
  margin: 30px 0;
}
</style>



