}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

footer {
  background: #f8fafc;
  height: 100px;
  margin-top: 50px;
}
h1, h2 {
  font-size: 37pt;
  font-weight: normal;
  color: #fff;
}
p {
  font-size: 27.5pt;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #2c3e50;
}
</style>



