import Vue from 'vue'
import Router from 'vue-router'
import Aboute from '@/components/Aboute'
import HomePage from '@/components/HomePage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'aboute',
      component: Aboute
    },
    {
      path: '/',
      name: 'homepage',
      component: HomePage
    }
  ]
})



