<template>
  <div class="conteiner-fluid">
    <div class="content">
      <h2>{{ desc }}</h2>
      <form id="signup-form" v-on:submit.prevent="processForm">
      <!-- name -->
      <div class="field">
        <label class="label">name</label>
        <input type="text" class="input" name="name">
      </div>
      <!-- email -->
      <div class="field">
        <label class="label">email</label>
        <input type="email" class="input" name="email">
      </div>
      <!-- submit button -->
      <div class="field has-text-right">
        <button type="submit" class="button is-danger">Отправить</button>
      </div>
    </form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      desc: 'Напишите мне'
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
$dark: #2c3e50
$orange: #FF6700
h1, h2
  color: $dark
  font-weight: normal
  margin: 30px 0 40px

p
  font-size: 27.5pt
ul
  list-style-type: none
  padding: 0
li
  display: inline-block
  margin: 0 10px
a
  color: #2c3e50
.field
  margin-top: 15px
  .input
    border: 1px solid #e5e5e5
    &:focus
      border: 1px solid $orange
  .button
    background: $orange
    border: 0
    box-shadow: none
    color: #fff
    padding: 6px 0
    width: 120px
</style>



