<template>
  <div class="projects">
    <div class="container height_full">
      <h2>{{ header }}</h2>
      <carousel :perPageCustom="[[768, 3], [1024, 1]]">
        <slide>
          <div class="row">
            <div class="col-md-4">
              <div class="photoWrapper">
                <img width="457" src="../assets/images/hbuc.jpg" alt="photo" class="photo-pos">
              </div>
            </div>
            <div class="col-md-4">
              <div class="photoWrapper">
                <img width="457" src="../assets/images/house.jpg" alt="photo" class="photo-pos">
              </div>
            </div>
            <div class="col-md-4">
              <div class="photoWrapper">
                <img width="457" src="../assets/images/flowers.jpg" alt="photo" class="photo-pos">
              </div>
            </div>
            <div class="col-md-4">
              <div class="photoWrapper">
                <img width="457" src="../assets/images/htmb.jpg" alt="photo" class="photo-pos">
              </div>
            </div>
            <div class="col-md-4">
              <div class="photoWrapper">
                <img width="457" src="../assets/images/re1.jpg" alt="photo" class="photo-pos">
              </div>
            </div>
            <div class="col-md-4">
              <div class="photoWrapper">
                <img width="457" src="../assets/images/landing.jpg" alt="photo" class="photo-pos">
              </div>
            </div>
          </div>
        </slide>
        <slide>
          Slide 2 Content
        </slide>
      </carousel>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Carouse',
  data () {
    return {
      header: 'Мои работы'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  transition: 0.2s ease-in-out;
  -webkit-transition: 0.2s ease-in-out;
  -moz-transition: 0.2s ease-in-out;
  -ms-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
}
h1, h2 {
  font-weight: normal;
  margin-bottom: 30px
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.projects {
  min-height: 500px;
  padding-top: 50px
}
.photoWrapper {
  height: 400px;
  overflow: hidden;
  padding-left: 50%;
  position: relative;
  width: 100%;
}
.photoWrapper::after {
  background: rgba(255,255,255, .4);
  display: block;
  content: '';
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
  transition: 0.2s ease-in-out;
  -webkit-transition: 0.2s ease-in-out;
  -moz-transition: 0.2s ease-in-out;
  -ms-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
}
.photoWrapper:hover::after {
  background: rgba(0,0,0, 0);
  z-index: 2;
}
.photo-pos {
  height: auto;
  position: relative;
  transform: translate3d(-50%, 0, 0);
  -webkit-transform: translate3d(-50%, 0, 0);
  width: calc(100% + 100%);
}
.row {
  margin: 0;
}
.col-md-4 {
  padding: 5px
}

</style>



